}

:root {
  --font-family: Avenir, sans-serif;
  --font-size-base: 16px;
  --font-size-small: 14px;
  --color-text: #3d4e61;
  --color-border: #e1e7ed;
  --box-shadow: 0 1px 8px #3d4e611a;
