  --gradient-focus: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

html {
  box-sizing: border-box;
  padding: 0;
}

body {
  box-sizing: border-box;
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  padding: 0;
}

.search-form {
  box-sizing: border-box;
  justify-content: center;
  width: 100%;
  margin: 20px auto 0;
  padding: 0;
  display: flex;
}

.search-form--big {
  height: 70px;
}

.search-form--small {
  height: 42px;
}

.search-form__label {
  box-sizing: border-box;
  width: 100%;
  padding: 0;
  display: block;
  position: relative;
}

.search-form__label:after {
  content: "";
  background: url("Search.ccd3c07b.svg") 0 0 / contain no-repeat;
  position: absolute;
}

.search-form__label--big:after {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search-form__label--small:after {
  width: 11px;
